.favourite-section {
  min-height: calc(100vh - (160px + 70px));
}
.favourite-container {
  padding: 20px 40px;
}
.favourite-section h2 {
  color: var(--color-purple);
  margin-bottom: 20px;
  font-weight: 600;
  font-size: 26px;
}
.favourite-cards {
  display: flex;
  flex-wrap: wrap;
}
@media screen and (max-width:990px) {
  .favourite-container {
    padding: 20px 10px;
  }
}